.short{
    z-index:2000;
    display: none;
    -webkit-transition: all ease 0.4s;
    transition: all ease 0.4s;
    cursor: pointer;
    position: fixed;
    top:0;
    left:0;
}
.contentTool{
    height:100vh;
    width:60vw;
    color: #ff652f;
    top:0;
    z-index:2000;
    background-color:#242424;
    left:0;
    opacity: 0;
    display:none;
    -webkit-transition: all ease 0.4s;
    transition: all ease 0.4s;
}

.contentShow{
    display: block;
    -webkit-transform: translateX(-180);
            transform: translateX(-180);
    opacity: 1;
}
